.wrap {
    width: 100%;
    height: 40px;
    background-color: black;
    position: fixed;
    z-index: 100;
    opacity: 0.8;
    display: flex;
    justify-content: center;
    .main {
        width: 85%;
        height: 40px;
        background-color: black;
        display: flex;
        align-items: center;
        justify-content: space-around;

        .item {
            text-align: center;
            text-decoration: none;
            color: white;
            font-family: "Times New Roman", Georgia, Serif;
            line-height: 100%;
        }
        .active {
            color: gray;
        }
    }
}
