*{
    padding: 0;
    margin: 0;
}
@keyframes fade-in {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
.home {
    height: 900px;
    text-align: center;
    h1 {
        font-family: Microsoft YaHei;
        opacity: 0;
        font-weight:bolder;
        font-size: xx-large;
        animation: fade-in 2s ease 0s 1; /*调用名称为fade-in的动画，全程动画显示时间4S，进入方式为ease，延时0S进入，播放次数1次*/
        animation-fill-mode: forwards; /*规定动画的最后状态为结束状态*/
    }
    h2 {
        // font-family: Microsoft YaHei;
        font-weight: normal;
        font-size:x-large;
        opacity: 0;
        animation: fade-in 2s ease 1s 1;
        animation-fill-mode: forwards;
    }
    .detail {
        animation: fade-in 3s ease 2s 1;
        animation-fill-mode: forwards;
    }
    .img {
        width: 25%;
    }
    .separator {
        background-color: lightgray;
        height: 10px;
        width: 100%;
    }
}

.show {
    position: relative;
    height: 3100px;
}

.show_img {
    position: absolute;
    top: 20%;
    right: 0;
    width: 40%;
    // height: 300px;
}

.show_text {
    position: absolute;
    top: 20%;
    left: 10%;
    width: 40%;
    height: 300px;
    opacity: 1;
    font-family: Microsoft YaHei;
    font-size:xx-large;
    font-weight: bolder;
    display: flex;
    justify-content: center;
    align-items: center;
}
.copyright{
    text-align: center;
    color: gray;
    font-size:medium ;
}
